import React, { useContext } from "react";
import { Box, SxProps, Theme, Typography } from "@mui/material";
import GameContext from "../../GameContext";

const ResourceBox = () => {
  const { resources, increments } = useContext(GameContext);
  const pairs = Object.keys(resources);

  return (
    <Box sx={rootSx}>
      {Object.entries(resources)
        .filter(([, amount]) => amount !== 0)
        .map(([type, amount]) => (
          <Box sx={resourceSx} key={`resource-${type}`}>
            <Typography variant="body1">{type}</Typography>
            <Typography variant="subtitle1">{Math.round(amount)}</Typography>
            <Typography variant="subtitle2">
              +
              {
                // @ts-ignore
                Math.round(increments[type])
              }
            </Typography>
          </Box>
        ))}
    </Box>
  );
};

export default ResourceBox;

const rootSx: SxProps<Theme> = {
  position: "fixed",
  mt: 2,
  px: 3,
  py: 1,
  borderRadius: 1.5,
  left: "50%",
  transform: "translateX(-50%)",
  background: "rgba(255,255,255,0.4)",
  backdropFilter: "blur(0.5rem)",
  display: "flex",
  flexDirection: "row",
};

const resourceSx: SxProps<Theme> = {
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
  mx: 1,
  ["& div"]: {
    display: "flex",
    flexDirection: "row",
    textTransform: "capitalize",
  },
};
