import React, {
  useCallback,
  useRef,
  useState,
  useImperativeHandle,
  useMemo,
  useContext,
  useEffect,
} from "react";
import { __MOUSE_LEFT_KEY_BUTTON__ } from "../../data/constants";
import { GameNode } from "../../data/idle-type";
import { NodeConfig } from "../../data/type";
import GameContext from "../../GameContext";

export interface NodeHandle {
  node: GameNode;
}

interface NodeProps {
  node: GameNode;
  config: NodeConfig;
  selected: boolean;
}

interface MemoNodeProps extends NodeProps {
  onHoverStart: (label: string) => void;
  onHoverEnd: () => void;
  onClick: (label: string) => void;
  onDoubleClick: (label: string) => void;
  forwardedRef: React.ForwardedRef<NodeHandle>;
}

const areEqual = (prevProps: MemoNodeProps, nextProps: MemoNodeProps) => {
  return (
    JSON.stringify(prevProps) === JSON.stringify(nextProps) &&
    prevProps.onDoubleClick === nextProps.onDoubleClick
  );
};

const MemoNode = React.memo(
  ({
    node,
    selected,
    config,
    onHoverStart,
    onHoverEnd,
    onClick,
    onDoubleClick,
    forwardedRef,
  }: MemoNodeProps) => {
    const circleRef = useRef<SVGCircleElement | null>(null);
    const textRef = useRef<SVGTextElement | null>(null);
    const [hover, setHover] = useState<boolean>(false);
    const animationDelay = useMemo(() => -Math.random() * 10, []);

    const {
      x,
      y,
      label,
      metadata: { count, limit },
    } = node;

    useImperativeHandle(forwardedRef, () => ({
      node,
    }));

    const getTextOffsetY = useCallback(
      ({ radius, verticalAlign, fontSize }: NodeConfig): number => {
        switch (verticalAlign) {
          case "top":
            return -radius - fontSize - 5;
          case "bottom":
            return radius + fontSize + 5;
          default:
            return 0;
        }
      },
      []
    );

    const handleMouseEnter = useCallback(() => {
      setHover(true);
      onHoverStart(label);
    }, []);

    const handleMouseLeave = useCallback(() => {
      setHover(false);
      onHoverEnd();
    }, []);

    const nodeStyle = useMemo(() => {
      if (count === 0) {
        return {
          filter: "grayscale(1)",
        };
      } else if (count === undefined) {
        return { display: "none" };
      }
    }, [limit, count]);

    return (
      <g
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        onClick={() => onClick(label)}
        onDoubleClick={() => onDoubleClick(label)}
        className={count === 0 ? "vibrate" : undefined}
        style={{
          cursor: "pointer",
          animationDelay: `${animationDelay}s`,
          ...nodeStyle,
        }}
      >
        {config.backgroundImage && (
          <defs>
            <pattern
              id={`node-${label}-background`}
              height="100%"
              width="100%"
              patternContentUnits="objectBoundingBox"
              viewBox="0 0 1 1"
            >
              <rect height="1" width="1" fill={config.color} />
              <image
                height="1"
                width="1"
                preserveAspectRatio={`${config.backgroundImageAlign} ${config.backgroundImageMeetOrSlice}`}
                href={config.backgroundImage}
              />
            </pattern>
          </defs>
        )}
        {(hover || selected) && (
          <circle
            cx={x}
            cy={y}
            r={config.radius + 2}
            stroke={config.strokeColor}
            fill="transparent"
          />
        )}
        <circle
          ref={circleRef}
          cx={x}
          cy={y}
          r={config.radius}
          stroke={config.strokeColor}
          fill={
            config.backgroundImage
              ? `url(#node-${label}-background)`
              : config.color
          }
        />
        <text
          ref={textRef}
          x={x}
          y={y + getTextOffsetY(config)}
          fontSize={config.fontSize}
          style={{ fill: config.fontColor }}
          textAnchor="middle"
          alignmentBaseline="middle"
        >
          {label}
        </text>
      </g>
    );
  },
  areEqual
);

const Node = React.forwardRef<NodeHandle, NodeProps>((props, ref) => {
  const {
    items,
    handleHoverGameNode,
    handleClickGameNode,
    handleDoubleClickGameNode,
  } = useContext(GameContext);

  return (
    <MemoNode
      {...props}
      onHoverStart={handleHoverGameNode}
      onHoverEnd={() => handleHoverGameNode(null)}
      onClick={handleClickGameNode}
      onDoubleClick={handleDoubleClickGameNode}
      forwardedRef={ref}
    />
  );
});

export default Node;
