import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
} from "react";
import AppContext from "./AppContext";
import { DEFAULT_GAME_STATE, DEFAULT_RESOURCES } from "./data/idle-constants";
import { Dependencies, GameNode, GameState, Resources } from "./data/idle-type";
import { Point } from "./data/type";

interface GameContextState {
  items: { [label: string]: number };
  resources: Resources;
  increments: Resources;
  gameNodes: { [label: string]: GameNode };
  hoverGameNode: string | null;
  selectedGameNode: string | null;
  handleClickBackground: (screenPoint: Point) => void;
  handleHoverGameNode: (label: string | null) => void;
  handleClickGameNode: (label: string | null) => void;
  handleDoubleClickGameNode: (item: string) => void;
}

const GameContext = React.createContext({} as GameContextState);

export const GameContextProvider = ({
  children,
}: {
  children: React.ReactElement;
}) => {
  const [gameState, setGameState] = useState<GameState>(
    JSON.parse(localStorage.getItem("gamestate") || "null") ||
      DEFAULT_GAME_STATE
  );
  const {
    graph: { nodes, edges },
  } = useContext(AppContext);
  const [hoverGameNode, setHoverGameNode] = useState<string | null>(null);
  const [selectedGameNode, setSelectedGameNode] = useState<string | null>(null);

  const dependencies = useMemo(
    () =>
      edges.reduce((acc, [u, v]) => {
        if (acc[u] === undefined) acc[u] = [];
        if (v === undefined) return acc;
        if (acc[v] === undefined) acc[v] = [];
        acc[v].push(u);
        return acc;
      }, {} as Dependencies),
    [edges]
  );

  const gameNodes = useMemo(
    () =>
      Object.entries(nodes).reduce((acc, [label, node]) => {
        acc[label] = {
          ...node,
          metadata: {
            count:
              gameState.items[label] ??
              (dependencies[label].every((item) => gameState.items[item])
                ? 0
                : undefined),
            limit: 1,
            costs: {
              population: 1,
            },
            effect: {
              population: {
                base: 0,
                factor: 1.1,
              },
            },
            description: "testing",
            type: "item",
            // @ts-ignore
            ...node.metadata,
          },
        } as GameNode;
        return acc;
      }, {} as { [label: string]: GameNode }),
    [nodes, gameState.items, dependencies]
  );

  const increments = useMemo((): Resources => {
    const ret = JSON.parse(JSON.stringify(DEFAULT_RESOURCES));
    for (const rType in ret) {
      // @ts-ignore
      ret[rType] = gameState.bases[rType] * gameState.factors[rType];
    }
    return ret;
  }, [gameState.bases, gameState.factors]);

  useEffect(() => {
    const timer = setInterval(() => {
      setGameState((prev) => {
        const resources = JSON.parse(JSON.stringify(DEFAULT_RESOURCES));
        for (const rType in resources) {
          // @ts-ignore
          resources[rType] =
            // @ts-ignore
            prev.resources[rType] + increments[rType] / 5;
        }

        return {
          ...prev,
          resources,
          lastTickerTime: Date.now(),
        };
      });
    }, 200);

    return () => {
      if (timer) {
        clearInterval(timer);
      }
    };
  }, [setGameState, increments]);

  const handleClickBackground = useCallback(
    (screenPoint: Point) => {
      console.log(screenPoint);
      setGameState((prev) => {
        const resources = JSON.parse(JSON.stringify(prev.resources));
        prev.clickResources.map((rType) => {
          resources[rType] += Math.max(increments[rType], 1);
        });

        return {
          ...prev,
          resources,
        };
      });
    },
    [setGameState, increments]
  );

  const handleClickGameNode = useCallback(
    (label: string | null) => {
      setSelectedGameNode(label);
    },
    [setSelectedGameNode]
  );

  const handleHoverGameNode = useCallback(
    (label: string | null) => {
      setHoverGameNode(label);
    },
    [setHoverGameNode]
  );

  const handleDoubleClickGameNode = useCallback(
    (item: string) => {
      setGameState((prev) => {
        if (prev.items[item] === gameNodes[item].metadata.limit) {
          return prev;
        }
        const effect = gameNodes[item].metadata.effect;
        for (const rType in effect) {
          // @ts-ignore
          prev.bases[rType] += effect[rType].base;
          // @ts-ignore
          prev.factors[rType] *= effect[rType].factor;
        }
        return {
          ...prev,
          bases: JSON.parse(JSON.stringify(prev.bases)),
          factors: JSON.parse(JSON.stringify(prev.factors)),
          items: {
            ...prev.items,
            [item]: prev.items[item] === undefined ? 1 : prev.items[item] + 1,
          },
        };
      });
    },
    [setGameState, gameNodes, dependencies]
  );

  return (
    <GameContext.Provider
      value={{
        items: gameState.items,
        resources: gameState.resources,
        increments,
        gameNodes,
        hoverGameNode,
        selectedGameNode,
        handleClickBackground,
        handleHoverGameNode,
        handleClickGameNode,
        handleDoubleClickGameNode,
      }}
    >
      {children}
    </GameContext.Provider>
  );
};

export default GameContext;
