import React from "react";
import { Button, SxProps, Theme } from "@mui/material";

interface IdleButtonProps {
  label: string;
  startIcon: React.ReactElement;
  onClick?: () => void;
}

const IdleButton = ({ label, startIcon, onClick }: IdleButtonProps) => {
  return (
    <Button
      sx={buttonSx}
      startIcon={startIcon}
      variant="contained"
      onClick={onClick}
      size="large"
    >
      {label}
    </Button>
  );
};

export default IdleButton;

const buttonSx: SxProps<Theme> = {
  pointerEvents: "all",
  flexDirection: "column",
  justifyContent: "center",
  "& .MuiButton-startIcon": {
    mr: 0,
    ml: 0,
  },
};
